<template>
  <b-navbar
    type="dark"
    variant="dark"
    class="d-flex flex-row justify-content-around mobile-navigation"
    fixed="bottom"
  >
    <router-link to="/">
      <b-button
        variant="transparent"
        class="text-white"
        aria-label="Homepage"
        ><b-icon icon="house-fill"></b-icon
      ></b-button>
    </router-link>

    <router-link to="/aboutme">
      <b-button
        variant="transparent"
        class="text-white"
        aria-label="About me"
        ><b-icon icon="person-square"></b-icon
      ></b-button>
    </router-link>

    <router-link to="/portfolio">
      <b-button
        variant="transparent"
        class="text-white"
        aria-label="Portfolio"
        ><b-icon icon="images"></b-icon
      ></b-button>
    </router-link>

    <b-button
      v-b-toggle.sidebar
      variant="transparent"
      class="text-white"
      aria-label="Sidebar"
      ><b-icon icon="layout-sidebar-inset"></b-icon
    ></b-button>

    <b-button
      @click="scrollToTop"
      v-show="visible"
      variant="transparent"
      aria-label="Scroll to top"
      class="text-white scroll-to-top"
    >
      <b-icon-arrow-up aria-label="Scroll to top" />
    </b-button>
  </b-navbar>
</template>
<script>
export default {
  name: "Navigation",
  data() {
    return {
      visible: false,
    };
  },
  methods: {
    scrollToTop: function () {
      this.intervalId = setInterval(() => {
        if (window.pageYOffset === 0) {
          clearInterval(this.intervalId);
        }
        window.scroll(0, window.pageYOffset - 50);
      }, 5);
    },
    scrollListener: function () {
      this.visible = window.scrollY > 400;
    },
  },
  mounted: function () {
    window.addEventListener("scroll", this.scrollListener);
  },
  beforeDestroy: function () {
    window.removeEventListener("scroll", this.scrollListener);
  },
};
</script>
<style lang="scss">
.mobile-navigation {
  max-width: 414px;
  height: 50px;
  margin: 0 25px 35px 25px;
  border: 0;
  border-radius: 20px;
  box-shadow: 0 5px 15px #00009b, 0 -15px 15px #131313;
  transition: all 250ms ease-in-out;
}

@media only screen and (min-width: 415px) {
  .mobile-navigation {
    margin: 0 auto 35px auto;
    transition: all 250ms ease-in-out;
  }
}

/* purgecss start ignore */
.scroll-to-top {
  cursor: pointer;
  animation: slideIn 250ms;
  transition: all 250ms ease-in-out;
}

@keyframes slideIn {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0%);
  }
}
@-webkit-keyframes slideIn {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0%);
  }
}
/* purgecss end ignore */
</style>