<template>
  <b-container
    class="d-flex flex-row justify-content-center align-items-center slowReveal"
  >
    <b-img id="memoji" :src="hire" class="memoji" />
    <b-button
      id="hire-button"
      size="sm"
      variant="primary"
      v-on:mouseover="mouseover"
      v-on:mouseleave="mouseleave"
      @click="onClick"
      >{{ message }}</b-button
    >
  </b-container>
</template>
<script>
import hi from "../assets/Images/memoji/hi.png";
import hire from "../assets/Images/memoji/hire.png";
import hope from "../assets/Images/memoji/hope.png";
import grateful from "../assets/Images/memoji/grateful.png";
export default {
  data() {
    return {
      me: hi,
      hire: hire,
      hope: hope,
      grateful: grateful,
      message: "Hire me, today!",
    };
  },
  methods: {
    mouseover: function () {
      this.hire = this.hope;
      this.message = "Don't hesitate!";
    },
    mouseleave: function () {
      this.hire = hire;
      this.message = "Hire me, today!";
    },
    onClick: function () {
      this.hire = grateful;
      this.message = "Thanks. Speak to you soon!";
      setTimeout(function () {
        window.location.href = "mailto:rovenlive@icloud.com";
      }, 1250);
    },
  },
};
</script>
<style lang="scss">
.memoji-wrapper {
  margin-top: 25%;
}
.memoji {
  max-width: 125px;
  transition: all 250ms ease-in-out;
}

.slowReveal {
  animation: slowFadeIn 6s forwards;
}

@keyframes slowFadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

$color-blue: #000077a8;
$color-white: white;
$color-grey: rgba(128, 128, 128, 0.719);

#hire-button {
  transform: translatey(0px);
  animation: float 5s ease-in-out infinite;
  text-align: center;
  letter-spacing: 3px;
  padding: 11px;
  font-size: 14px;
  border-radius: 15px;
  box-shadow: 20px 20px $color-blue;
  margin-top: -100px;
}
#hire-button:after {
  transform: translatey(0px);
  animation: float2 5s ease-in-out infinite;
  content: ".";
  text-shadow: 22px 22px $color-grey;
  text-align: left;
  font-size: 55px;
  width: 55px;
  height: 11px;
  border-radius: 11px;
  background-color: $color-white;
  position: absolute;
  display: block;
  bottom: -30px;
  left: 0;
  box-shadow: 22px 22px $color-grey;
  z-index: -2;
}

@keyframes float {
  0% {
    transform: translatey(0px);
  }
  50% {
    transform: translatey(-20px);
  }
  100% {
    transform: translatey(0px);
  }
}

@keyframes float2 {
  0% {
    line-height: 30px;
    transform: translatey(0px);
  }
  55% {
    transform: translatey(-20px);
  }
  60% {
    line-height: 10px;
  }
  100% {
    line-height: 30px;
    transform: translatey(0px);
  }
}
</style>