// Importing dependencies
import Vue from 'vue'
import VueRouter from 'vue-router'

// Importing Views
import Home from '@/views/Home'
import About from '@/views/About'
import Portfolio from '@/views/Portfolio'

Vue.use(VueRouter)

// Creating routes
const router = new VueRouter({
  base: './Web_resfolio/',
  mode: 'history',
  routes: [
    { // Home
      path: '/',
      name: 'Home',
      component: Home
    },
    { // AboutMe
      path: '/aboutme',
      name: 'AboutMe',
      component: About,
      meta: {
        title: 'About me'
      }
    },
    { // Portfolio
      path: '/portfolio',
      name: 'Portfolio',
      component: Portfolio,
      meta: {
        title: 'Portfolio'
      }
    },
    // Otherwise redirect to home
    { path: '*', redirect: '/' }
  ]
})

export default router
