<template>
  <b-img :src="showDifferent"  class="portfolio-collage" />
</template>
<script>
import collagePortfolio from "../../assets/Images/Portfolio/multiple-portfolio-sm.webp"
import collage from "../../assets/Images/Portfolio/multiple-sm.webp"
export default {
    data() {
        return {
            portfolio: collagePortfolio,
            collage: collage
        }
    },
    computed: {
      showDifferent: function() {
        if (this.$route.path.includes("portfolio")) {
          return this.collage
        }
        else return this.portfolio
      }
    }
}
</script>
<style>
.portfolio-collage {
  max-width: 100%;
  margin: 0 auto;
  border-top-left-radius: 50px;
  border-top-right-radius: 50px;
}
</style>